import React from 'react'
import {
  BrowserRouter as Router,
  Route
} from 'react-router-dom'
import LeftToRight from './components/LeftToRight'
import { Grid, Container } from 'semantic-ui-react'


const App = () => {




  return (
    <Router>
      <Container>
        <Route path="/" render={() => <LeftToRight />} />
      </Container>
    </Router>
  )
}

export default App
