import React from 'react'

import { Step, Icon} from 'semantic-ui-react'


const StepExplanations = ({ activeSelection }) => {

    const steps = [
        { title: 'Tilaaja', description: 'Tilaajatyyppi', icon: 'user' },

        { title: 'Aineistot', description: 'Vain Kelan rekisteriaineistoja?', icon: 'database' },
        { title: 'Käyttötarkoitus', description: 'Tietojen käyttötarkoitus?', icon: 'law' },
        { title: 'Taso', description: 'Tilasto vai yksilötason aineisto', icon: 'chart bar' },
        { title: 'Lakiperuste', description: 'Lakiperuste ja luvanantaja/-päätöksentekijä', icon: 'gavel' },
    ]

    return(
        <Step.Group fluid  size='mini'>
        
        {
            steps.map((step,idx) => {
                const isCompleted = idx < activeSelection
                const isActive = idx === activeSelection
                const isDisabled = !isCompleted && !isActive

                return(
                    <Step completed={isCompleted} active={isActive} disabled={isDisabled}>
                        <Icon name={ step.icon } />
                        <Step.Content>
                            <Step.Title>{ step.title }</Step.Title>
                            <Step.Description>{ step.description }</Step.Description>
                        </Step.Content>
                    </Step>
                )
            })
        }


      
      </Step.Group>
    )
}

export default StepExplanations