import React, { useState } from 'react'
import Selectable from './Selectable'
import { Grid, Card, Segment } from 'semantic-ui-react'
import { findOptions, findCombinations, combinations, numberOfOptions } from '../utils/toisio'

import  uuidv4  from 'uuid/v4'
import Result from './Result'
import StepExplanations from './StepExplanations'

const LeftToRight = () => {

    const [activeSelection, setActiveSelection] = useState(0)
    const [selected,setSelected] = useState([])
    const [result,setResult] = useState(null)
    const [possibleCombinations,setPossibleCombinations] = useState(combinations)



    const onSelect = (value,idx) => {
        let copySelected = [...selected]
        copySelected[idx] = value
        copySelected = copySelected.slice(0,idx+1)
        setSelected( copySelected )
        setActiveSelection(  copySelected.length   )
        setPossibleCombinations(findCombinations([...copySelected]))

        // 
        if (idx === (numberOfOptions-1)) {
            console.log('resulting', possibleCombinations[0])
            setResult( possibleCombinations[0].slice(numberOfOptions,numberOfOptions+2) )
        } else {
            setResult(null)
        }
    }

    //console.log('possibleCmbo', possibleCombinations, selected , activeSelection, numberOfOptions)

    return(
        <Grid>

            <Grid.Row>
            <StepExplanations activeSelection={activeSelection} />
            </Grid.Row>
                
            <Grid.Row>
                <Grid columns={5}>
                    {
                        [0,1,2,3].map(idx => {

                            return(
                                                            
                            <Grid.Column>
                                <Segment padded>
                                    <Selectable
                                        idx = {idx}
                                        activeSelection={activeSelection}
                                        possibleCombinations={possibleCombinations}
                                        key={ uuidv4() }
                                        selected={ selected[idx] }
                                        options={ findOptions(idx) }
                                        disabled={ idx > activeSelection }
                                        onSelect={ (value) => onSelect(value,idx) } /> 
                                </Segment>
                            
                            </Grid.Column>

                            )
                        })
                    }
                    <Grid.Column>
                    <Grid.Row stretched>
                        
                            <Result result={result}></Result>  
                        
                    </Grid.Row>

                    </Grid.Column>
                </Grid>
            </Grid.Row>
        </Grid>
            
    )

}


export default LeftToRight