import React from 'react'
import { Icon, Grid, Segment, Header } from 'semantic-ui-react'


const Result = ({ result }) => {
    
    if (!result) {
        return('')
    }
    

    //const result = ['Ei lakiperustetta', 'FINDATA']
    console.log('ResultArray', result)
    const style = { color: '#21ba45' }
    
    return(
        <Segment >
        <Grid textAlign='center'>
            <Grid.Row verticalAlign='middle'>
                <Icon name='law' size='huge'  style={style}/>
            </Grid.Row>
            <Grid.Row verticalAlign='middle'>
            
               <Header as='h3'>
                   {  result[0] }
                </Header>
            </Grid.Row>
            {
                createResultPermit(result)
            }
        </Grid>
             
        </Segment>
    )
    

}

const createResultPermit = result => {
    if (result.filter(res => res!=='').length < 2) {
        return('')
    } 
    const style = { color: '#21ba45' }

    return(
        <>
            <Grid.Row verticalAlign='middle'>
                <Icon name='gavel' size='huge' style={style} />
            </Grid.Row>
            <Grid.Row verticalAlign='middle'>
                <Header as='h3'>
                    {  result[1] }
                </Header>
            </Grid.Row>
        </>
    )
}


export default Result