import React from 'react'
import { Button, Card } from 'semantic-ui-react'
import  uuidv4  from 'uuid/v4'


const Selectable = ({ options, disabled, selected, onSelect, idx, activeSelection, possibleCombinations }) => {


    if (!Array.isArray(options)) {
        return('')
    }


    return(
        <Card.Group horizontal="true">
            
            {
                options.map(opt => {

                    const color = selected === opt ? 'green' : 'blue'

                    const isValidCombination = possibleCombinations.find(combo => combo[idx] === opt) ? true : false

                    return(
                        <Card key={ uuidv4() }>
                            <Button size={'large'} color={color} disabled={ disabled | ( !isValidCombination  && idx === activeSelection) } onClick={() => onSelect(opt)}>
                                {opt}
                            </Button>
                        </Card>
                    )
                })
            }
            
        </Card.Group>
    )
}


export default Selectable