import { combinations as rawCombinations } from './combinations'



const names = ["Tilaaja", "Pyynnon.kohde", "Lakiperuste.kayttotarkoitus", "Tiedon.taso", "Luvan.myontaja.ja.lupa.", "Minne.tiedot", "Huom."]

const get = o => names.map(n => o[n]).slice(0,6)


export const combinations = rawCombinations.map(get)


export const findOptions = idx => {
    return combinations.map(combination => combination[idx]).reduce((t,c) => t.includes(c) ? t : t.concat(c) ,[])
}

export const numberOfOptions = combinations[0].length - 2


export const findCombinations = (combos) => {


    let res = [...combinations]
    let desiredValue
    let comboIndex = 0
    while (combos.length>0) {
        desiredValue = combos.shift()
        res = res.filter(combination => combination[comboIndex] === desiredValue  )
        comboIndex = comboIndex +1 
    }

    return res

}




